<template>
    <div id="new-ad">
        <h4>Nuevo Aviso</h4>
        <div class="row">
            <form @submit.prevent="saveAd" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <select name="category" id="category" v-model="category" required>
                            <option value="" disabled selected>Elige una categoría</option>
                            <option value="NECESITO">NECESITO</option>
                            <option value="ALQUILO">ALQUILO</option>
                            <option value="VENDO">VENDO</option>
                            <option value="TRASPASO">TRASPASO</option>
                            <option value="ANTICRESIS">ANTICRESIS</option>
                            <option value="SERVICIOS">SERVICIOS</option>
                            <option value="CLASES-CURSOS">CLASES-CURSOS</option>
                            <option value="OTROS">OTROS</option>
                        </select>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <textarea @keyup="contador" v-model="ad" class="materialize-textarea"></textarea>
                        <label>Aviso (Máximo de letras de 20 a 180)</label>
                        <div class="chip orange darken-2 white-text">{{con}}</div>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="cellphone" required>
                        <label>Celular</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <select name="reseller" id="reseller" v-model="reseller" required>
                            <option value="" disabled selected>Elige un proveedor de avisos</option>
                            <option value="AVISOS ANGELES">AVISOS ANGELES</option>
                        </select>
                    </div>
                </div>
                <button type="submit" class="btn green">Guardar</button>
                <router-link to="/" class="btn red">Cancelar</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit';
import firebase from 'firebase';

export default {
    name: 'new-ad',
    data(){
        return{
            ad_id: null,
            category: null,
            ad: null,
            cellphone: null,
            date: null,
            reseller: null
        }
    },
    mounted: function(){
        $('select').formSelect();
    },
    methods:{
        saveAd(){
            var new_ad = db.collection("ads").doc();
            new_ad.set({                
                ad_id: new_ad.id,
                category: this.category,
                ad: this.ad,
                cellphone:  this.cellphone,
                reseller: this.reseller,
                date: firebase.firestore.FieldValue.serverTimestamp()
            })
            .then(docRef => this.$router.push('/'))
            .catch(err => console.log)
        },
        contador(){
          this.con = cw(this.ad);
        }

    }
}

function cw(str){
  return str.split('').length;
}
</script>



