<template>
  <div id="login">
    <div class="container">
    <div class="row">
      <div class="col s12 m10 offset-m2">
        <div class="login card-panel orange darken-2 white-text center">
          <h5>Acceso :</h5>
          <form>
            <div class="input-field">
              <i class="material-icons prefix">email</i>
              <input type="email" id="email" v-model="email">
              <label class="white-text" for="email">Correo</label>
            </div>
            <div class="input-field">
              <i class="material-icons prefix">lock</i>
              <input type="password" id="password" v-model="password">
              <label class="white-text" for="password">Contraseña</label>
            </div>
            <button v-on:click="login" class="btn btn-large btn-extended grey lighten-4 black-text">Entrar</button>
          </form>
          <pre>{{ $data }}</pre>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>

<script>
import firebase from 'firebase';

export default {
    name: 'login',
    data: function(){
        return{
            email: '',
            password: ''
        }
    },
    methods:{
        login: function(e){
            firebase.auth().signInWithEmailAndPassword(this.email, this.password)
                .then(
                    user => {
                        //alert('usuario conectado');
                        //this.$router.go({ path: this.$router.path });
                        //this.$router.push('/')
                        this.$router.replace('new-ad')
                    },
                    err => {
                        alert(err.message);
                    }
                );
            e.preventDefault();
        }
    }
}
</script>



