import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import NewAd from '@/components/NewAd'
import EditAd from '@/components/EditAd'
import ViewAd from '@/components/ViewAd'
import Login from '@/components/Login'
import firebase from 'firebase'

Vue.use(Router)

export default new Router({
  routes: [
    /*{
      path: '*',
      redirect: '/home'
    },*/
    /*{
      path: '/',
      redirect: '/home'
    },*/
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/new',
      name: 'new-ad',
      component: NewAd
      //meta: {
      //  autentificado: true
      //}
    },
    {
      path: '/edit/:ad_id',
      name: 'edit-ad',
      component: EditAd
    },
    {
      path: '/:ad_id',
      name: 'view-ad',
      component: ViewAd
    }
  ]
});

