<template>
    <div id="edit-ad">
        <h4>Editar Aviso</h4>
        <div class="row">
            <form @submit.prevent="updateAd" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="ad_id" disabled>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="category" disabled>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <textarea v-model="ad" class="materialize-textarea"></textarea>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="cellphone" required>
                    </div>
                </div>
                <button type="submit" class="btn green">Guardar</button>
                <router-link to="/" class="btn red">Cancelar</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit';

export default {
    name: 'edit-ad',
    data(){
        return{
            ad_id: null,
            category: null,
            ad: null,
            cellphone: null,
        }
    },
    beforeRouteEnter(to, from, next){
        db.collection('ads').where('ad_id','==',to.params.ad_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm => {
                        vm.ad_id = doc.id
                        vm.category = doc.data().category
                        vm.ad = doc.data().ad
                        vm.cellphone = doc.data().cellphone
                      })
                })
            })
    },
    watch:{
        '$route':'fetchData'
    },  
    methods:{
        fetchData(){
            db.collection('ads').where('ad_id','==',this.$route.params.ad_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.ad_id = doc.data().ad_id
                        this.category = doc.data().category
                        this.ad = doc.data().ad
                        this.cellphone = doc.data().cellphone
                    })
                })
        },
        updateAd(){
            db.collection('ads').where('ad_id','==',this.$route.params.ad_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            //ad_id: this.ad_id,
                            category: this.category,
                            ad: this.ad,
                            cellphone: this.cellphone
                            //date: this.date
                        })
                        .then(() => {
                            this.$router.push({name:'view-ad',params:{ad_id: this.ad_id}})
                        })
                    })
                })
        }
    }
}
</script>



