<template>
    <div id="home">
        <ul class="collection with-header">
            <li v-for="ad in ads" v-bind:key="ad.id" class="collection-item">
                <div class="chip orange darken-2 white-text">{{ad.category}}</div> {{ad.date}}
                <router-link v-bind:to="{name:'view-ad', params: {ad_id: ad.ad_id}}"><i class="fa fa-eye fa-lg right"></i></router-link><br>            
                <div class="ad">{{ad.ad}}</div>
            </li>
        </ul>
        
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large green"><i class="fa fa-plus"></i></router-link>
        </div>
    </div>
</template>

<script>
import db from "./firebaseInit";
import firebase from 'firebase';
import moment from 'moment';

moment.locale('es');

export default {
    name: 'home',
    data(){
        return{
            ads: []
        }
    },
    created(){
        db.collection('ads').orderBy('date','desc').get()
            .then(querySnapshot => {
                this.loading = false;
                querySnapshot.forEach(doc => {
                    const data = {
                        ad_id: doc.data().ad_id,
                        category: doc.data().category,
                        ad: doc.data().ad,
                        cellphone: doc.data().cellphone,
                        date: moment(doc.data().date).fromNow()
                        
                    };
                    this.ads.push(data);
                    console.log(doc.data().date)
                });
            });
    }
}
</script>

<style scoped>
.ad{
    display: block;
    display: -webkit-box;
    height: 14px * 1.4 * 3;
    font-size: 14px;
    line-height: 1.4;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}

.fa-eye{
    color: #f57c00;
}
</style>

