<template>
    <div id="view-ad">
        <ul class="collection with-header">
            <li class="collection-item"><div class="chip orange darken-2 white-text">{{category}}</div></li>
            <li class="collection-item">{{ad}}</li>
            <li class="collection-item"><div class="chip orange darken-2 white-text"><i class="fa fa-phone"></i></div>{{cellphone}}</li>
            <li class="collection-item"><div class="chip orange darken-2 white-text"><i class="fa fa-calendar"></i></div>Publicado {{date}}</li>
            <li class="collection-item"><div class="chip orange darken-2 white-text"><i class="fa fa-user-circle"></i></div>Publicado en {{reseller}} </li>
        </ul>
        <router-link to="/" class="btn orange darken-2">Volver</router-link>
        <button @click="deleteAd" class="btn red">Eliminar</button>
        <button @click="exportPdf" class="btn blue"><i class="fa fa-print"></i></button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name:'edit-ad', params:{ad_id: ad_id}}" class="btn-floating btn-large green"><i class="fa fa-pencil"></i></router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit';
import moment from 'moment';
import * as jsPDF from 'jspdf';

export default {
    name: 'view-ad',
    data(){
        return{
            ad_id: null,
            category: null,
            ad: null,
            cellphone: null,
            date: null
        }
    },
    beforeRouteEnter(to, from, next){
        db.collection('ads').where('ad_id','==',to.params.ad_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm => {
                        vm.ad_id = doc.data().ad_id
                        vm.category = doc.data().category
                        vm.ad = doc.data().ad
                        vm.cellphone = doc.data().cellphone
                        vm.reseller = doc.data().reseller
                        vm.date = moment(doc.data().date).fromNow()
                    })
                })
            })
    },
    watch:{
        '$route':'fetchData'
    },  
    methods:{
        fetchData(){
            db.collection('ads').where('ad_id','==',this.$route.params.ad_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.ad_id = doc.data().ad_id
                        this.category = doc.data().category
                        this.ad = doc.data().ad
                        this.cellphone = doc.data().cellphone
                        this.reseller = doc.data().reseller
                        this.date = doc.data().date
                    })
                })
        },
        deleteAd(){
            if(confirm('De momento no puede eliminar.')){
                //db.collection('ads').where('ad_id','==',this.$route.params.ad_id).get()
                //    .then(querySnapshot => {
                //        querySnapshot.forEach(doc => {
                //            doc.ref.delete()
                //            this.$router.push('/')
                //        })
                //    })
            }
        },
        exportPdf(){
        
        var doc = new jsPDF('l','pt');
        doc.setFont('arial')
        doc.setFontType('normal')
        doc.setFontSize(128);
        doc.text(this.category, 14, 100);
        doc.line(14, 105, 800, 105);
        doc.line(14, 106, 800, 106);
        doc.line(14, 107, 800, 107);
        doc.line(14, 108, 800, 108);
        doc.line(14, 109, 800, 109);
    
        //doc.setTextColor(100);
        var pageWidth = doc.internal.pageSize.width || doc.internal.pageSize.getWidth();

        var y_ad = 0;

        //alert(cw(this.ad));

        if (cw(this.ad) >= 20 && cw(this.ad) <=40 ){
          //alert('esta entre 20 - 40');
          doc.setFontSize(94); //CASO RARO AUMENTE LA LETRA +4
          y_ad = 200;
        } else if ( cw(this.ad) >= 41 && cw(this.ad) <=50 ) { //OK
          //alert('esta entre 41 - 50');
          doc.setFontSize(86);
          y_ad = 197;              
        } else if ( cw(this.ad) >= 51 && cw(this.ad) <=60 ) { //OK
          //alert('esta entre 51 - 60');
          doc.setFontSize(82);
          y_ad = 194;               
        } else if ( cw(this.ad) >= 61 && cw(this.ad) <=70 ) { //OK
          //alert('esta entre 61 - 70');
          doc.setFontSize(76);
          y_ad = 191;                         
        } else if ( cw(this.ad) >= 71 && cw(this.ad) <=80 ) { //OK
          //alert('esta entre 71 - 80');
          doc.setFontSize(72);
          y_ad = 189;               
        } else if ( cw(this.ad) >= 81 && cw(this.ad) <=90 ) { //OK
          //alert('esta entre 81 - 90');
          doc.setFontSize(68);
          y_ad = 187;               
        } else if ( cw(this.ad) >= 91 && cw(this.ad) <=100 ) { //OK
          //alert('esta entre 91 - 100');
          doc.setFontSize(64);
          y_ad = 185;               
        } else if ( cw(this.ad) >= 101 && cw(this.ad) <=110 ) { //OK
          //alert('esta entre 101 - 110');
          doc.setFontSize(60);
          y_ad = 183;                         
        } else if ( cw(this.ad) >= 111 && cw(this.ad) <=120 ) { //OK
          //alert('esta entre 111 - 120');
          doc.setFontSize(56);
          y_ad = 180;               
        } else if ( cw(this.ad) >= 121 && cw(this.ad) <=130 ) { // OJO =
          //alert('esta entre 121 - 130');
          doc.setFontSize(56);
          y_ad = 177;                         
        } else if ( cw(this.ad) >= 131 && cw(this.ad) <=140 ) { //OK
          //alert('esta entre 131 - 140');
          doc.setFontSize(54);
          y_ad = 174;
        } else if ( cw(this.ad) >= 141 && cw(this.ad) <=150 ) { // OK
          //alert('esta entre 141 - 150');
          doc.setFontSize(54);
          y_ad = 171;          
        } else if ( cw(this.ad) >= 151 && cw(this.ad) <=160 ) { // OK
          //alert('esta entre 151 - 160');
          doc.setFontSize(50);
          y_ad = 168;                    
        } else if ( cw(this.ad) >= 161 && cw(this.ad) <=170 ) {
          //alert('esta entre 161 - 170');
          doc.setFontSize(49);
          y_ad = 165;                              
        } else if ( cw(this.ad) >= 171 && cw(this.ad) <=180 ) { 
          //alert('esta entre 171 - 180');
          doc.setFontSize(48);
          y_ad = 162;                              
        } else if ( cw(this.ad) >= 181 && cw(this.ad) <=220 ) { //OK
          //alert('esta entre 181 - 220');
          doc.setFontSize(40);
          y_ad = 162; 
        } else if ( cw(this.ad) >= 121 && cw(this.ad) <=240 ) { // OK
          //alert('esta entre 121 - 240');
          doc.setFontSize(38);
          y_ad = 162;           
        }

        var text = doc.splitTextToSize(this.ad, pageWidth - 35, {});
        doc.text(text, 14, y_ad);
          
        var x_cellphone = 0;
        //place and size, cellphone
        if (cw(this.cellphone) >= 9 && cw(this.cellphone) <=10 ){
          //alert('esta entre 9 - 10');
          doc.setFontSize(40);
          x_cellphone = 600; //OK
        } else if ( cw(this.cellphone) >= 10 && cw(this.cellphone) <=24 ) {
          //alert('esta entre 21 - 24');
          doc.setFontSize(40);
          x_cellphone = 430; //OK
        } else if ( cw(this.cellphone) >= 33 && cw(this.cellphone) <=35 ) { 
          //alert('esta entre 33 - 35');
          doc.setFontSize(40);
          x_cellphone = 265; //OK
        }

        doc.setFontSize(32);
        doc.text('CEL. ' + this.cellphone, x_cellphone, 580);
        //doc.line(14, 550, 800, 550);
        doc.save('appavisos-' + this.ad_id + '.pdf');
      }
    }
}

function cw(str){
  return str.split('').length;
}

</script>



